import React from 'react';

const Portfolio = () => {
  const projects = [
    { title: "Project 1", video: "/video1.mp4" },  // Example for local video
    { title: "Project 2", video: "/video2.mp4" },  // Example for external video
    { title: "Project 3", video: "/video3.mp4" },  // Another local video
    { title: "Project 4", video: "/video6.mp4" },  // Another local video (add more if needed)
  ];

  return (
    <section id="portfolio" className="py-20">
      <div className="container mx-auto text-center">
        <h1 className="text-4xl font-bold mb-10">Portfolio</h1>
        <h2 className="text-4xl font-bold mb-10 mt-8">Shorts</h2>
        <div className="flex justify-center space-x-6 mt-6">
  {/* TikTok Icon */}
  <a href="https://www.tiktok.com/@yourprofile" target="_blank" rel="noopener noreferrer">
    <i className="fab fa-tiktok text-3xl text-black hover:text-purple-600"></i>
  </a>

  {/* Instagram Reels Icon */}
  <a href="https://www.instagram.com/reel/yourreel" target="_blank" rel="noopener noreferrer">
    <i className="fab fa-instagram text-3xl text-black hover:text-pink-600"></i>
  </a>

  {/* YouTube Shorts Icon */}
  <a href="https://youtube.com/shorts/yourvideo" target="_blank" rel="noopener noreferrer">
    <i className="fab fa-youtube text-3xl text-black hover:text-red-600"></i>
  </a>
</div>

        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 mt-10">
          {projects.map((project, index) => (
            <div key={index} className="shadow-lg rounded-lg overflow-hidden">
              {/* Video Card */}
              <div className="relative">
                <video
                  autoPlay
                  muted
                  loop
                  controls
                  className="w-full h-full object-cover"
                  style={{ maxHeight: '600px' }}  // Adjust height for smaller cards
                >
                  <source
                    src={project.video}
                    type="video/mp4"
                  />
                  Your browser does not support the video tag.
                </video>
              </div>
            </div>
          ))}
        </div>

        {/* this is the section for shorts which went viral */}
        
        <div className="mt-20 text-center">
  <h2 className="text-4xl font-bold mb-10">Videos That Go Viral!</h2>

  <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
    {/* Image 1 */}
    <a href="https://youtube.com/shorts/ZcboBHn-lNk?si=1InWE6EpDljGNBCr" target="_blank" rel="noopener noreferrer">
      <img
        src="/p1.png" // Replace with actual image path
        alt="Video 1"
        className="w-full h-auto max-w-[1080px] max-h-[1920px] object-cover rounded-lg"
      />
    </a>

    {/* Image 2 */}
    <a href="https://youtube.com/shorts/L_4a8CcQHo0?si=mMNMNeq_-nTSa9Gz" target="_blank" rel="noopener noreferrer">
      <img
        src="/p2.png" // Replace with actual image path
        alt="Video 2"
        className="w-full h-auto max-w-[1080px] max-h-[1920px] object-cover rounded-lg"
      />
    </a>

    {/* Image 3 */}
    <a href="https://www.instagram.com/reel/CukHO7tAfTK/" target="_blank" rel="noopener noreferrer">
      <img
        src="/p3.png" // Replace with actual image path
        alt="Video 3"
        className="w-full h-auto max-w-[1080px] max-h-[1920px] object-cover rounded-lg"
      />
    </a>

    {/* Image 4 */}
    <a href="https://www.instagram.com/reel/CvaKtRQARr0/" target="_blank" rel="noopener noreferrer">
      <img
        src="/p4.png" // Replace with actual image path
        alt="Video 4"
        className="w-full h-auto max-w-[1080px] max-h-[1920px] object-cover rounded-lg"
      />
    </a>
  </div>


          {/* music video section */}

          <div className="mt-20 text-center mt-8">
  <h2 className="text-4xl font-bold mb-10">Music Video</h2>
  </div>

  <div className="mt-20 text-center mt-8">
  <h2 className="text-4xl font-bold mb-10">Podcast</h2>
  <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
    <div>
    <iframe className=" rounded-lg shadow-lg hover:shadow-xl transition-shadow duration-300"
    width="600" height="338" src="https://www.youtube.com/embed/DJN53lqaLQk" 
    title="Bailey Farren: On Recovering from Burnout and Adopting the Right Habits to Accomplish Anything" 
    frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>
    </div>
    <div>
    <iframe className=" rounded-lg shadow-lg hover:shadow-xl transition-shadow duration-300"
    width="600" height="338" src="https://www.youtube.com/embed/YHU7NVFrpI4" 
    title="American Venture Capitalist Explains India’s BRIGHT FUTURE by 2030 | Bhaskar Ghosh | Neon Show" 
    frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>

    </div>
  </div>

      </div>

</div>

      </div>
    </section>
  );
};

export default Portfolio;
